.app-wrapper {
    display: grid;
    width: 1200px;
    min-height: 500px;
    margin: 0 auto;

    grid-template-areas: "h h" "n c";

    grid-template-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
    grid-gap: 10px;
}


.app-wrapper-content {
    background-color: whitesmoke;
    margin-top: 10px;
}

